.menu-label {
  display: inline-block;
  position: absolute;
  top: 1rem;
  right: 1rem;
  transition: background 0.2s;
  margin: 0;
  border: 0;
  border-radius: 3px;
  box-shadow: none;
  background-color: var(--brand-color);
  cursor: pointer;
  padding: 0;
  width: 45px;
  height: 45px;
  overflow: hidden;
  text-indent: -200%;
  font-size: 0;
  appearance: none; }
  .menu-label:focus {
    outline: none; }
  .menu-label span {
    display: block;
    position: absolute;
    top: 21px;
    right: 10px;
    left: 10px;
    background: rgba(255, 255, 255, 0.5);
    height: 3px; }
    .menu-label span::before, .menu-label span::after {
      display: block;
      position: absolute;
      left: 0;
      background-color: rgba(255, 255, 255, 0.5);
      width: 100%;
      height: 3px;
      content: ''; }
    .menu-label span::before {
      top: -8px; }
    .menu-label span::after {
      bottom: -8px; }

.menu-label-close {
  display: block;
  border-bottom: 1px solid rgba(255, 255, 255, 0.5);
  padding: .5rem;
  width: 300px;
  font-size: 2rem; }
  .menu-label-close:hover {
    cursor: pointer;
    color: white; }

.grid-wrapper {
  position: relative;
  transition: var(--menu--transition);
  background-color: #fff;
  overflow-x: hidden; }

.menu--push-right {
  position: fixed;
  top: 0;
  right: 0;
  transform: translate3d(300px, 0, 0);
  transition: var(--menu--transition);
  z-index: -1;
  background-color: #67b5d1;
  width: 300px;
  height: 100%;
  overflow-x: hidden;
  overflow-y: overlay; }

.menu-trigger {
  display: none;
  margin: 0; }
  .menu-trigger:checked ~ .grid-wrapper {
    transform: translate3d(-300px, 0, 0); }
  .menu-trigger:checked ~ .menu--push-right {
    transform: translate3d(0, 0, 0);
    z-index: 200; }
